html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  height: 58.45em;
  margin: 0;
  padding: 0;
}

:root {
  --very-dark-magenta: hsl(300, 43%, 22%);
  --soft-pink: hsl(333, 80%, 67%);
  --dark-grayish-magenta: hsl(303, 10%, 53%);
  --light-grayish-magenta: hsl(300, 24%, 96%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-spartan: "Spartan", sans-serif;
}

h1, h2, h3, h4, p {
  font-family: var(--font-spartan);
  margin: 0;
}

.hero {
  height: 100%;
  background-color: var(--white);
  position: relative;
  overflow: hidden;
}
.hero::before {
  content: "";
  position: absolute;
  background-image: url("../../../images/bg-pattern-top-desktop.svg");
  background-repeat: no-repeat;
  background-size: 40em;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.hero::after {
  content: "";
  position: absolute;
  background-image: url("../../../images/bg-pattern-bottom-desktop.svg");
  background-size: 80em;
  background-position: 100% 0;
  background-repeat: no-repeat;
  top: 10em;
  left: 0;
  width: 100%;
  height: 100%;
}
.hero__container {
  padding: 5rem 10rem;
  display: flex;
  flex-direction: column;
}
.hero__container .startup {
  display: flex;
  margin-bottom: 4rem;
}
.hero__container .startup__info .title h2 {
  color: var(--very-dark-magenta);
  font-size: 3rem;
  font-weight: 700;
}
.hero__container .startup__info .description {
  margin-top: 1rem;
}
.hero__container .startup__info .description p {
  color: var(--dark-grayish-magenta);
  font-size: 1rem;
  width: 30em;
  line-height: 1.4;
}
.hero__container .startup__stars {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.hero__container .startup__stars .testimonial {
  background: var(--light-grayish-magenta);
  display: flex;
  flex-direction: row;
  margin-bottom: 1rem;
  padding: 1rem 4rem 1rem 1rem;
  border-radius: 10px;
  z-index: 999;
}
.hero__container .startup__stars .testimonial .stars {
  display: flex;
  align-items: center;
}
.hero__container .startup__stars .testimonial .stars img:not(:last-child) {
  padding-right: 0.4rem;
}
.hero__container .startup__stars .testimonial .text {
  margin-left: 2rem;
  margin-top: 0.2rem;
}
.hero__container .startup__stars .testimonial:nth-child(1) {
  margin-left: -3rem;
}
.hero__container .startup__stars .testimonial:nth-child(2) {
  margin-left: 3rem;
}
.hero__container .startup__stars .testimonial:nth-child(3) {
  margin-left: 10rem;
}
.hero__container .cards {
  display: flex;
}
.hero__container .cards .card {
  background: var(--very-dark-magenta);
  border-radius: 10px;
  height: 15em;
}
.hero__container .cards .card .profile {
  display: flex;
  padding: 2rem;
  align-items: center;
}
.hero__container .cards .card .profile img {
  margin-right: 1rem;
  border-radius: 50%;
  width: 12%;
}
.hero__container .cards .card .profile .user__name {
  color: var(--white);
}
.hero__container .cards .card .profile .user__post {
  color: var(--soft-pink);
  font-size: 0.9rem;
  font-weight: 500;
  margin-top: 0.2rem;
}
.hero__container .cards .card .description {
  padding: 0 2rem 2rem 2rem;
  color: var(--light-grayish-magenta);
  font-size: 0.9rem;
  line-height: 1.4;
}
.hero__container .cards .card:not(:last-child) {
  margin-right: 2rem;
}
.hero__container .cards .card:nth-child(2) {
  margin-top: 1rem;
}
.hero__container .cards .card:nth-child(3) {
  margin-top: 2rem;
}